* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.main {
  width: 100%;
  height: 100vh;
  background: green;
  display: flex;
  align-items: center;

  button {
    background: brown;
    width: 300px;
    height: 50px;
    border-radius: 10px;
    font-size: 20px ;
    letter-spacing: 3px;
    color: #fff;
    box-shadow: 0 0 5px silver;
    border: none;

  }
}